.main-btn {
  box-sizing: border-box;
  position: relative;
  overflow: hidden;
  font-size: 20px;
  background: transparent;
  overflow: visible;
  height: 2em;
  -webkit-transition: all 0.8s linear;
  transition: all 0.8s linear; }
  .main-btn button {
    -webkit-backface-visibility: hidden;
    backface-visibility: hidden;
    position: relative;
    -webkit-transition: 0.5s color ease;
    transition: 0.5s color ease;
    text-decoration: none;
    overflow: visible; }
    .main-btn button:before, .main-btn button:after {
      content: "";
      -webkit-transition: 0.5s all ease;
      transition: 0.5s all ease;
      -webkit-backface-visibility: hidden;
      backface-visibility: hidden;
      position: absolute;
      width: 0;
      height: 0.18em; }
    .main-btn button:after {
      bottom: -0.2em; }
    .main-btn button:before {
      top: -0.2em; }
    .main-btn button:after {
      left: 0;
      width: 100%;
      height: 0.2em; }
    .main-btn button:before {
      right: 0;
      width: 100%;
      height: 0.2em; }
    .main-btn button:hover:after, .main-btn button:hover:before {
      width: 0; }
  .main-btn:before, .main-btn:after {
    content: "";
    box-sizing: border-box;
    -webkit-transition: 0.25s all ease;
    transition: 0.25s all ease;
    -webkit-backface-visibility: hidden;
    backface-visibility: hidden;
    position: absolute;
    width: 0.15em;
    height: calc(100% - 0.2em); }
  .main-btn:before {
    left: 0; }
  .main-btn:after {
    right: 0;
    bottom: 0.2em; }
  .main-btn:hover {
    -webkit-transform: scale(0.9);
    transform: scale(0.9);
    opacity: 0.9; }
    .main-btn:hover:before, .main-btn:hover:after {
      height: 0; }

